<template>
  <div> 
      <ul>
          <li v-for=" i in data_arr">
						<img :src="i.img" alt="">
						<h3>数量{{i.num}}  价格：{{i.jiage}}</h3>
			</li>
      </ul>
      <h4>总价{{zongjia}}</h4>
  </div>
</template>


<script>
import {
		mapGetters,
		mapActions
	} from 'vuex'
export default {
     methods:{
  	...mapActions(['s_num','s_num_j'])
  },
  computed:{
  	...mapGetters(['data_arr','zongjia'])
  },
}
</script>

<style>
a{
	text-decoration: none;
}
</style>


