<template>
  <div id="no1">
<div class="clearfix" style="margin-bottom:1.25rem;">
    <div v-for="i in data_arr" style="float:left; text-align:center; color:red;">
      <div>
          <img :src="i.img" alt="" style="width:300px;height:300px;">
      </div>
      <h3>{{i.title}}</h3>
      <h4>价格：{{i.jiage}}</h4>
      <h5><button style="width:1.25rem;height:1.25rem;font-size: 1rem;" @click="s_num_j(i)">-</button>数量{{i.num}}<button style="width:1.25rem;height:1.25rem;font-size: 1rem;" @click="s_num(i)">+</button></h5>
      
    </div>
    </div>

    <h1 style="color:white;background:green;text-align:center;" @click="jiesuan"><a href="#/no2">结算</a></h1>
  </div>
</template>

<script>
import {
		mapGetters,
		mapActions
	} from 'vuex'
export default {
     methods:{
  	...mapActions(['s_num','s_num_j','jiesuan'])
  },
  computed:{
  	...mapGetters(['data_arr'])
  }
}
</script>

<style>
</style>


