import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

var state = {
	data_arr:[
		{img:'https://p0.ssl.qhimgs4.com/t011d7db4621289a326.jpg',title:'猪肉大葱馅',jiage:'30',num:0},
		{img:'https://www.kmway.com/upload/resources/image/2017/02/14/510640.jpg',title:'鲜虾馅',jiage:'50',num:0},
	],
	zongjia:0
}
var getters = {
	data_arr(state){
		return state.data_arr
	},
	zongjia(state){
		return state.zongjia
	}
}
var mutations = {
	s_num(state,data){
		for(var i = 0 ; i < state.data_arr.length; i++){
			if(state.data_arr[i].title == data.title){
				state.data_arr[i].num ++
			}
			// state.zongjia =state.data_arr[i].num * state.data_arr[i].jiage 
		}
		 
	},
	s_num_j(state,data){
		for(var i = 0 ; i < state.data_arr.length; i++){
			if(state.data_arr[i].title == data.title){
				if(state.data_arr[i].num -- <= 0){
					state.data_arr[i].num = 0
				}else{
					state.data_arr[i].num --
				}
			}
		}
	},
	s_jiesuan(state){
		state.zongjia  = 0
		for(var i = 0 ; i < state.data_arr.length; i++){
			state.zongjia +=state.data_arr[i].num * state.data_arr[i].jiage
		}
	}
}
var actions = {
	s_num({commit},data){
		commit('s_num',data)
	},
	s_num_j({commit},data){
		commit('s_num_j',data)
		console.log(data)
	},
	jiesuan({commit}){
		commit('s_jiesuan')
	}
}
export default new Vuex.Store({
	state,
	getters,
	mutations,
	actions
})


