<template>
  <div id="app">
	  <div style="height: 2.5rem;background: red;font-size: 1rem;line-height: 2.5rem;"><a href="#/" style="font-size: 1rem;margin: 0 1.25rem;">></a>庆华包子铺（窦店）</div>
    <router-view></router-view>
  </div>
</template>

<script>
	import {
		mapGetters,
		mapActions
	} from 'vuex'
export default {
  name: 'app',
  methods:{
  	...mapActions(['s_num','s_num_j'])
  },
  computed:{
  	...mapGetters(['data_arr'])
  },
  data () {
    return {
    }
  }
}
</script>

<style>
*{
  margin: 0;
  padding: 0;
}
	*{
		margin:0px;
		padding:0px;
		text-decoration:none;
		list-style:none;
		}
	.clearfix::after{
		content:'';
		display:block;
		width:0px;
		height:0px;
		visibility:hidden;
		clear:both;
		}
	.clearfix{
		zoom:1;
	}
</style>



